'use client';

import { useState, useRef } from 'react';
import axios from 'axios';
import Image from 'next/image';
import TiptapEditor from './components/TiptapEditor';
import html2pdf from 'html2pdf.js';
import TurndownService from 'turndown';

// âœ… Types
type Video = {
  id: {
    videoId: string;
  };
  snippet: {
    title: string;
    publishedAt: string;
    thumbnails: {
      medium: {
        url: string;
      };
    };
  };
};

type Blog = {
  title: string;
  content: string;
  highlights: string[];
  tips: string[];
};

export default function YouTubeGallery() {
  const [channelInput, setChannelInput] = useState('');
  const [videos, setVideos] = useState<Video[]>([]);
  const [selectedVideos, setSelectedVideos] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);
  const [editableBlog, setEditableBlog] = useState<Blog | null>(null);

  const blogContentRef = useRef<HTMLDivElement>(null);

  const getChannelIdFromQuery = async (query: string): Promise<string | null> => {
    const YOUTUBE_API_KEY = process.env.NEXT_PUBLIC_YOUTUBE_API_KEY;
    const url = `https://www.googleapis.com/youtube/v3/search?key=${YOUTUBE_API_KEY}&q=${encodeURIComponent(
      query
    )}&type=channel&part=snippet`;
    try {
      const res = await axios.get(url);
      return res.data.items[0]?.snippet?.channelId || null;
    } catch (err) {
      console.error('Error fetching channel ID:', err);
      return null;
    }
  };

  const getRecentVideos = async (channelId: string): Promise<void> => {
    setLoading(true);
    const YOUTUBE_API_KEY = process.env.NEXT_PUBLIC_YOUTUBE_API_KEY;
    const url = `https://www.googleapis.com/youtube/v3/search?key=${YOUTUBE_API_KEY}&channelId=${channelId}&part=snippet&order=date&maxResults=6`;
    try {
      const res = await axios.get(url);
      setVideos(res.data.items);
    } catch (err) {
      console.error('Error fetching videos:', err);
    }
    setLoading(false);
  };

  const fetchVideos = async () => {
    const channelId = await getChannelIdFromQuery(channelInput);
    if (!channelId) return;
    await getRecentVideos(channelId);
  };

  const toggleVideoSelection = (videoId: string) => {
    setSelectedVideos((prev) =>
      prev.includes(videoId) ? prev.filter((id) => id !== videoId) : [...prev, videoId]
    );
  };

  const generateCombinedBlog = async () => {
    const urls = selectedVideos.map((id) => `https://www.youtube.com/watch?v=${id}`);
    try {
      const res = await axios.post<Blog>('http://127.0.0.1:8000/generate-blog', {
        youtube_urls: urls,
      });
      setEditableBlog(res.data);
    } catch (err) {
      console.error('Blog generation failed', err);
    }
  };

  const downloadPDF = () => {
    if (!blogContentRef.current) return;
    const element = blogContentRef.current;

    const opt = {
      margin: 0.5,
      filename: `${editableBlog?.title || 'blog'}.pdf`,
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
    };

    html2pdf().set(opt).from(element).save();
  };

  const downloadMarkdown = () => {
    if (!editableBlog) return;

    const turndownService = new TurndownService();
    const markdown = turndownService.turndown(editableBlog.content);

    const blob = new Blob([markdown], { type: 'text/markdown' });
    const url = URL.createObjectURL(blob);

    const link = document.createElement('a');
    link.href = url;
    link.download = `${editableBlog.title || 'blog'}.md`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">YouTube Blog Generator</h1>

      <div className="flex gap-2 mb-4">
        <input
          type="text"
          placeholder="Enter YouTube Channel Name or URL"
          value={channelInput}
          onChange={(e) => setChannelInput(e.target.value)}
          className="border px-3 py-2 rounded w-full"
        />
        <button onClick={fetchVideos} className="bg-blue-600 text-white px-4 py-2 rounded">
          Fetch Videos
        </button>
      </div>

      {loading && <p>Loading videos...</p>}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {videos.map((video) => (
          <div key={video.id.videoId} className="border rounded p-3">
            <Image
              src={video.snippet.thumbnails.medium.url}
              alt={video.snippet.title}
              width={320}
              height={180}
              className="rounded mb-2"
            />
            <div className="flex items-center gap-2 mb-2">
              <input
                type="checkbox"
                checked={selectedVideos.includes(video.id.videoId)}
                onChange={() => toggleVideoSelection(video.id.videoId)}
              />
              <h2 className="font-semibold text-lg">{video.snippet.title}</h2>
            </div>
            <p className="text-sm text-gray-600">{video.snippet.publishedAt}</p>
          </div>
        ))}
      </div>

      {selectedVideos.length > 0 && (
        <button
          className="mt-4 bg-purple-600 text-white px-4 py-2 rounded"
          onClick={generateCombinedBlog}
        >
          Generate Combined Blog ({selectedVideos.length})
        </button>
      )}

      {editableBlog && (
        <div className="mt-8 p-4 border rounded bg-gray-100 space-y-4">
          <div ref={blogContentRef}>
            {/* Title Section */}
            <div className="space-y-2">
              <label className="block font-semibold">Blog Title</label>
              <input
                className="text-xl font-bold w-full border px-3 py-2 rounded"
                value={editableBlog.title}
                onChange={(e) => setEditableBlog({ ...editableBlog, title: e.target.value })}
              />
            </div>

            {/* Content Editor */}
            <div className="space-y-2">
              <label className="block font-semibold">Blog Content</label>
              <TiptapEditor
                content={editableBlog.content}
                onChange={(val) => setEditableBlog({ ...editableBlog, content: val })}
              />
            </div>

            {/* Highlights Section */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <label className="block font-semibold">Highlights</label>
                <button
                  onClick={() => {
                    setEditableBlog({
                      ...editableBlog,
                      highlights: [...editableBlog.highlights, ''],
                    });
                  }}
                  className="bg-blue-500 text-white px-3 py-1 rounded text-sm"
                >
                  + Add Highlight
                </button>
              </div>
              <div className="space-y-2">
                {editableBlog.highlights.map((h, i) => (
                  <div key={i} className="flex items-center gap-2">
                    <input
                      className="flex-1 border px-3 py-2 rounded"
                      value={h}
                      onChange={(e) => {
                        const newHighlights = [...editableBlog.highlights];
                        newHighlights[i] = e.target.value;
                        setEditableBlog({ ...editableBlog, highlights: newHighlights });
                      }}
                      placeholder="Enter highlight..."
                    />
                    <button
                      onClick={() => {
                        const newHighlights = editableBlog.highlights.filter((_, index) => index !== i);
                        setEditableBlog({ ...editableBlog, highlights: newHighlights });
                      }}
                      className="bg-red-500 text-white px-3 py-2 rounded"
                    >
                      Remove
                    </button>
                  </div>
                ))}
              </div>
            </div>

            {/* Tips Section */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <label className="block font-semibold">Tips</label>
                <button
                  onClick={() => {
                    setEditableBlog({
                      ...editableBlog,
                      tips: [...editableBlog.tips, ''],
                    });
                  }}
                  className="bg-blue-500 text-white px-3 py-1 rounded text-sm"
                >
                  + Add Tip
                </button>
              </div>
              <div className="space-y-2">
                {editableBlog.tips.map((t, i) => (
                  <div key={i} className="flex items-center gap-2">
                    <input
                      className="flex-1 border px-3 py-2 rounded"
                      value={t}
                      onChange={(e) => {
                        const newTips = [...editableBlog.tips];
                        newTips[i] = e.target.value;
                        setEditableBlog({ ...editableBlog, tips: newTips });
                      }}
                      placeholder="Enter tip..."
                    />
                    <button
                      onClick={() => {
                        const newTips = editableBlog.tips.filter((_, index) => index !== i);
                        setEditableBlog({ ...editableBlog, tips: newTips });
                      }}
                      className="bg-red-500 text-white px-3 py-2 rounded"
                    >
                      Remove
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Download buttons */}
          <div className="flex gap-4 mt-4">
            <button
              onClick={downloadPDF}
              className="bg-green-600 text-white px-4 py-2 rounded"
            >
              Download as PDF
            </button>

            <button
              onClick={downloadMarkdown}
              className="bg-gray-800 text-white px-4 py-2 rounded"
            >
              Download as Markdown
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
