'use client';

import { useEditor, EditorContent } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Underline from '@tiptap/extension-underline';
import Link from '@tiptap/extension-link';
import { useEffect, useState } from 'react';

interface TiptapEditorProps {
    content: string;
    onChange: (content: string) => void;
}

export default function TiptapEditor({ content, onChange }: TiptapEditorProps) {
    const editor = useEditor({
        extensions: [
            StarterKit,
            Underline,
            Link.configure({
                openOnClick: false,
            }),
        ],
        content,
        onUpdate({ editor }) {
            onChange(editor.getHTML());
        },
    });

    useEffect(() => {
        if (editor && editor.getHTML() !== content) {
            editor.commands.setContent(content);
        }
    }, [content, editor]);

    const [linkUrl, setLinkUrl] = useState('');
    const [showLinkInput, setShowLinkInput] = useState(false);

    if (!editor) {
        return null;
    }

    const chain = editor.chain();


    return (
        <div>
            {/* Toolbar */}
            <div className="mb-2 space-x-2 border p-2 rounded bg-gray-100">
                <button
                    onClick={() => editor.chain().focus().toggleBold().run()}
                    disabled={!editor.can().chain().focus().toggleBold().run()}
                    className={editor.isActive('bold') ? 'font-bold text-blue-600' : ''}
                    type="button"
                >
                    Bold
                </button>

                <button
                    onClick={() => editor.chain().focus().toggleItalic().run()}
                    disabled={!editor.can().chain().focus().toggleItalic().run()}
                    className={editor.isActive('italic') ? 'italic text-blue-600' : ''}
                    type="button"
                >
                    Italic
                </button>

                <button
                    onClick={() => editor.chain().focus().toggleUnderline().run()}
                    disabled={!editor.can().chain().focus().toggleUnderline().run()}
                    className={editor.isActive('underline') ? 'underline text-blue-600' : ''}
                    type="button"
                >
                    Underline
                </button>

                <button
                    onClick={() => chain.focus().toggleStrike().run()}
                    disabled={!editor.can().chain().focus().toggleStrike().run()}
                    className={editor.isActive('strike') ? 'line-through text-blue-600' : ''}
                    type="button"
                >
                    Strike
                </button>

                <button
                    onClick={() => chain.focus().toggleBulletList().run()}
                    className={editor.isActive('bulletList') ? 'text-blue-600' : ''}
                    type="button"
                >
                    Bullet List
                </button>

                <button
                    onClick={() => chain.focus().toggleOrderedList().run()}
                    className={editor.isActive('orderedList') ? 'text-blue-600' : ''}
                    type="button"
                >
                    Numbered List
                </button>

                {!editor.isActive('link') ? (
                    <>
                        <button
                            onClick={() => setShowLinkInput(!showLinkInput)}
                            type="button"
                            className="underline text-blue-600"
                        >
                            Add Link
                        </button>
                        {showLinkInput && (
                            <span>
                                <input
                                    type="text"
                                    value={linkUrl}
                                    onChange={(e) => setLinkUrl(e.target.value)}
                                    placeholder="Enter URL and press Add"
                                    className="border px-1 py-0.5 rounded ml-2"
                                />
                                <button
                                    onClick={() => {
                                        if (linkUrl) {
                                            chain.focus().extendMarkRange('link').setLink({ href: linkUrl }).run();
                                        }
                                        setLinkUrl('');
                                        setShowLinkInput(false);
                                    }}
                                    type="button"
                                    className="ml-1 px-2 py-1 bg-green-500 text-white rounded"
                                >
                                    Add
                                </button>
                                <button
                                    onClick={() => {
                                        setShowLinkInput(false);
                                        setLinkUrl('');
                                    }}
                                    type="button"
                                    className="ml-1 px-2 py-1 bg-gray-300 rounded"
                                >
                                    Cancel
                                </button>
                            </span>
                        )}
                    </>
                ) : (
                    <button
                        onClick={() => chain.focus().unsetLink().run()}
                        type="button"
                        className="underline text-red-600"
                    >
                        Remove Link
                    </button>
                )}

                <button onClick={() => chain.focus().undo().run()} type="button">
                    Undo
                </button>
                <button onClick={() => chain.focus().redo().run()} type="button">
                    Redo
                </button>
            </div>

            {/* Editor content */}
            <EditorContent editor={editor} className="border rounded p-2 bg-white min-h-[200px]" />
        </div>
    );
}
